.nav {
  position: fixed;
  top: 0px;
  width: 100%;
  display: flex;
  padding: 20px;
  height: 30px;

  z-index: 1;

  transition-timing-function: ease-in;
  transition: all 0.5s;
}
.nav_logo {
  position: fixed;
  left: 30px;
  width: 120px;
  object-fit: contain;
}

.nav_black {
  background-color: #111;
}

.nav_link {
  margin-right: 10px;
  object-fit: contain;
  color: white;
  text-decoration: none;
  font-size: 20px;
  padding: 10px;
}

.nav_links {
  margin-left: 140px;
  position: fixed;
  left: 30px;
  width: 100%;
  object-fit: contain;
}
.nav_links_right {
  margin-left: 1200px;
  position: fixed;
  left: 30px;
  width: 100%;
  object-fit: contain;
}
